.section__details {
  display: flex !important; // Переопределяем grid на flex
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 40px 20px;

 .details_btn {
    background-color: #fff;
    color: #000;
    font-weight: 600;
    font-size: 16px;
    padding: 16px 61px;
    border: none;
    border-radius: 9999px; // круглая кнопка
    cursor: pointer;
    display: inline-block;
    text-align: center;
    margin-top: 32px;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #eaeaea;
    }
  }

  .section-title {
    font-weight: 500;
    font-size: 48px;
    line-height: 100%;
    letter-spacing: -1.5px;
    text-align: center;
    align-items: center;
    color: #fff;
  }

  .details__grid {
    display: flex;
    flex-direction: column; // или row, если будет много .details__item
    align-items: center;
    gap: 24px;
    width: 100%;
    max-width: 800px; // Ограничим ширину, чтобы не расползалось
  }

  .details__item {
    display: flex;
    flex-direction: column;
    align-items: center;

    .details__img {
      width: 100%;
      max-width: 100%;
      height: auto;
      object-fit: cover;
      border-radius: 8px;
      display: block;
      margin-bottom: 12px;
    }

    .details__title {
      font-size: 18px;
      font-weight: 700;
      color: #fff;
      margin-top: 12px;
      text-align: left;
    }

    .details__text {
      font-size: 14px;
      color: #ccc;
      margin-top: 6px;
      line-height: 1.5;
      text-align: left;
      max-width: 65%;
    }

    .details__images {
        display: flex;
        gap: 12px; // расстояние между картинками
        justify-content: center;
        width: 100%;
        margin-bottom: 12px;
      }

      .details__img {
        width: 100%;
        max-width: 36%;
        height: auto;
        object-fit: cover;
        border-radius: 8px;
        display: block;
      }

      .details__img--first {
        max-width: 68%; // чуть меньше, чтобы не слипались
      }

      .details__img--reserve {
        max-width: 36%;
      }

       .details__img--reserve-2 {
        max-width: 68%;
      }
  }
}

