
/*nav*/

.nav {
  &__link {
    position: relative;
    color: #111;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    font-weight: 600;

    &::after {
      content: '';
      position: absolute;
      display: block;
      bottom: -8px;
      width: 100%;
      height: 1px;
      background-color: #111;

      transition: transform 0.3s;
      transform: scaleX(0);
      transform-origin: left;
    }
    &:hover::after {
      transform: scaleX(1);
    }
  }

  &__list {
    margin-top: 36px;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
}

