/*Header Section */

.section__about {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.section__about-text{
  max-width: 498px;
  font-size: 23px;
  line-height: 140%;
  letter-spacing: 0px;
  font-weight: 500;

  @media (min-width: 567px) {
    font-weight: 400;
  }
}

.product__photo {
  max-width: 100%;
  height: auto;
  object-fit: contain;
  display: block;
  margin: 0 auto;
}

.footer {
  z-index: -1;
}

.footer__image {
  z-index: -1;
  width: 100%;
  height: 555px;
  object-fit: cover;
}
