.section-title {
    font-weight: 500;
    font-size: 48px;
    line-height: 100%;
    letter-spacing: -1.5px;
    text-align: center;
    align-items: center;

    @media  (min-width: 768px) {
        font-size: 40px;
    } 
    
    @media  (min-width:  1024px) {
        font-size: 48px;
        
    }

    &--left {
        text-align: left;
    }
}