.header {
  box-sizing: border-box;
  height: 100vh;
  background-image: url(/src/images/bike-bg_full.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__bottom {
    padding: 0 30px 50px;
  }
}


.title {
  font-size: 80px;
  text-align: center;
  font-weight: bold;
  letter-spacing: -1.5px;
  line-height: 100%;
}

