.section__contacts {
  padding: 60px 20px;
  color: #fff;
  text-align: center;

  .section-title {
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 40px;
  }

  .contacts__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 40px;
    max-width: 1000px;
    margin: 0 auto;
  }

  .contacts__form {
    flex: 1;
    min-width: 280px;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .contacts__input,
  .contacts__textarea {
    padding: 16px 20px;
    border: none;
    border-radius: 30px;
    font-size: 14px;
    font-family: inherit;
  }

  .contacts__textarea {
    height: 120px;
    resize: none;
    border-radius: 20px;
  }

  .contacts__button {
    background-color: #fff;
    color: #000;
    font-weight: 600;
    font-size: 16px;
    padding: 16px 48px;
    border: none;
    border-radius: 9999px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .contacts__button:hover {
    background-color: #eaeaea;
  }

  .contacts__info {
    flex: 1;
    min-width: 250px;
    max-width: 400px;
    text-align: left;
    font-size: 14px;

    p {
      margin-bottom: 20px;
    }

    a {
      color: #fff;
      text-decoration: none;
    }

    a:hover {
      text-decoration: underline;
    }
  }
}
