.product {
    &photo{
        width: 100%;
    }

    &__title {
        font-weight: 600;
        font-size: 24px;
        line-height: 140%;
    }

    &__price {
        font-weight: 600;
        font-size: 24px;
        line-height: 140%;
    }
}

.section {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 96px;
}

.section__compare-bikes {
    @media  (max-width: 768px) {
        grid-template-columns: repeat(1, 1fr);
        
    }
}

.section-title {
  grid-column: 1 / -1;
}