.top-bar {
  padding: 36px;
  display: flex;
  justify-content: space-between;
  align-items: center;

   @media (min-width: 576px) {
   padding: 36px 72px;
  }


  &__icons {
    display: flex;
    gap: 24px;
  }

  &__logo {
    display: flex;

    transition: transform 0.3s;

  &:hover {
    transform: scale(1.1);
  }
  }
}

