.page {
  font-family: "Poppins", sans-serif;
  color: #fff;
  font-size: 16px;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    opacity: 0;
     transition: all 0.4s;
    transform: translateX(-100%);
    pointer-events: none;


    &:target {
      opacity: 1;
      pointer-events: all;

       transform: translateX(0);
    }
  }
}
