.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #C2C2C2;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;

  &__nav {
    margin-bottom: 40px;
  }

  &__content {
    padding-inline: 36px;
  }

  &__phone-number {
    font-size: 16px;
    line-height: 27px;
    color: #111;
    text-decoration: none;
    padding-bottom: 16px;
    display: block;
  }

  &__phone-call {
    font-size: 16px;
    line-height: 27px;
    color: #111;
    text-decoration: none;
    border-bottom: 1px solid #111;
    width: fit-content;
    padding-bottom: 10px;
  }
}
