/*ICONS SECTION */
.icon {
  width: 24px;
  height: 24px;
  display: block;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.1);
  }

  @media (min-width: 576px) {
    width: 32px;
    height: 32px;
  }
}

.icon--phone {
  background-image: url(/src/images/Icon-Phone-call.png);
}

.icon--menu {
  background-image: url(/src/images/Icon-Burger-menu.png);
}

.icon--close {
  background-image: url(/src/images/Icon-Close.png);
}
